<template>
  <div>
    <form action="/">
      <van-search
        v-model="value"
        show-action
        background="#4fc08d"
        placeholder="请输入搜索关键词"
        @search="onSearch"
        @cancel="onCancel"
        @input="mjk"
      />
    </form>
    <ul v-for="(item, index) in list" :key="index">
      <li @click="lilist(index)">
        <i class="iconfont icon-sousuo">{{ item.title }}</i>
      </li>
    </ul>
    <div class="Search">
      <p>
        历史搜索<span @click="empty">清除<i class="iconfont icon-shanchu"></i></span>
      </p>
      <li v-for="(iten,index) in historyList" :key="index" @click="lis(index)">{{ iten }}</li>
    </div>
  </div>
</template>

<script>
export default {
  name: "sousuo",
  data() {
    return {
      value: "",
      index: 0,
      list: [],
      text: localStorage.getItem("sezrch"),
      historyList: [],
    };
  },
  mounted() {
    //如果本地存储的数据historyList有值，直接赋值给data中的historyList
    if (JSON.parse(localStorage.getItem("historyList"))) {
      this.historyList = JSON.parse(localStorage.getItem("historyList"));
    }
  },
  methods: {
    onSearch() {
      this.mjk;
    },
    mjk() {
      this.axios
        .post(
          "https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/search",
          {
            key_word: this.value,
            limit: 10000,
          }
        )
        .then((res) => {
          console.log(res.data.data);
          this.list = res.data.data;
        });
    },
    onCancel() {
      this.$router.go(-1);
    },
    lilist(val) {
      if (!this.historyList.includes(this.value)) {
        this.historyList.unshift(this.value);
        localStorage.setItem("historyList", JSON.stringify(this.historyList));
      } else {
        //有搜索记录，删除之前的旧记录，将新搜索值重新push到数组首位
        let i = this.historyList.indexOf(this.value);
        this.historyList.splice(i, 1);
        this.historyList.unshift(this.value);
        localStorage.setItem("historyList", JSON.stringify(this.historyList));
      }
      this.$router.push({
        path: "./xiangqing",
        query: {
          id: this.list[val]._id,
          cate_id: this.list[val].cate_id,
        },
      });
    },
    // *******************
    lis(val){
      this.value=this.historyList[val]
      this.mjk()
    },
    // ************************清除历史记录
    empty(){
      this.$toast.success('清空历史搜索成功');
            localStorage.removeItem('historyList');
            this.historyList = [];
    }
  }
};
</script>

<style scoped>
ul {
  padding: 5px;
}
ul li {
  width: 100%;
  border: 1px solid #ededed;
  line-height: 40px;
}
.Search {
  width: 95%;
  padding: 5px;
}
.Search span {
  float: right;
}
.Search li{height: 30px;float: left;margin: 10px;text-align: center;line-height: 30px;border-radius: 30px;border: 1px solid #ededed;padding: 5px;background: pink;}
</style>