<template>
  <div>
    <div class="top">
      <span @click="fanhui"><i class="iconfont icon-fanhui"></i></span>
      <h3>列表详情</h3>
    </div>
    <div class="list">
      <h3>{{ xqlist.title }}</h3>
      <div class="zuozhe">
        <img :src="xqlist.avatar" alt="" />
        <p class="van-ellipsis">作者:{{ xqlist.nickname }}</p>
        <p style="font-size: 10px">
          发布时间:{{
            new Date(parseInt(xqlist.time))
              .toLocaleString()
              .replace(/:\d{1,2}$/, "")
          }}
        </p>
        <button @click="dingyueyes" v-if="xqlist.is_fav">已收藏</button>
        <button @click="dingyue" v-else>+收藏</button>
      </div>
      <div class="myimg">
        <h6>{{ xqlist.content }}</h6>
        <li>
          <img
            :src="item"
            alt=""
            v-for="(item, index) in xqlist.imageSrc"
            :key="index"
          />
        </li>
      </div>
    </div>
    <div class="footr">
      <span>评论：{{ xqlist.comment }}</span>
      <span>点赞：{{ xqlist.like }}</span>
    </div>
    <!-- 评论列表 -->
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
    >
      <div class="pllb" v-for="(item, index) in list" :key="index">
        <img :src="item.info.avatar" alt="" />
        <li>{{ item.info.nickname }}</li>
        <li>
          {{
            new Date(parseInt(item.create_time))
              .toLocaleString()
              .replace(/:\d{1,2}$/, "")
          }}
        </li>
        <div class="pl">
          <h5>{{ item.content }}</h5>
          <span class="pan">
            <van-icon
              name="chat-o"
              :badge="item.reply_num"
              size="25"
              @click="showPopup(index)"
          />
        </span>
          <span class="pan" v-if="item.is_like"
            ><van-icon name="like" size="25" @click="unlike(item)" color="red"
          /></span>
          <span v-else class="pan"
            ><van-icon name="like-o" size="25" @click="like(item)"
          /></span>
        </div>
      </div>
      <!-- *************************回复评论列表 -->
      <van-popup v-model="show" closeable position="bottom" :style="{ height: '80%' }">
        <van-field v-model="value" label="回复" placeholder="请输入内容" />
      <button @click="hfpl" style="width:100%;background: #488ab3;border: 1px solid #488ab3;border-radius: 10px;">发布</button>
      <div class="hfpllb" v-for="(item1, index) in hfpllb" :key="index">
        <img :src="item1.info.avatar" alt="" />
        <li>{{ item1.info.nickname }}</li>
        <li>
          {{
            new Date(parseInt(item1.create_time))
              .toLocaleString()
              .replace(/:\d{1,2}$/, "")
          }}
        </li>
        <p>{{ item1.content }}</p>
      </div>
    </van-popup>
    </van-list>

    <!-- 底部 -->
    <div class="dibu">
      <input type="text" placeholder="写评论" v-model="inputtext" />
      <van-icon
        name="chat-o"
        :badge="xqlist.comment || ''"
        size="25"
        @click="pinglun"
      />
      <van-icon
        name="like"
        size="25"
        @click="undianzan"
        color="red"
        v-if="xqlist.is_like"
      />
      <van-icon name="like-o" size="25" @click="dianzan" v-else />
      <van-icon name="star" size="25" v-if="xqlist.is_fav" color="red" />
      <van-icon name="star-o" size="25" v-else />
      <van-icon name="share-o" size="25" @click="showShare = true"/>
      <van-share-sheet v-model="showShare" title="立即分享给好友" :options="options" @select="onSelect"/>
    </div>
    <van-popup v-model="shown"><img :src="myimg" alt=""></van-popup>
  </div>
</template>

<script>
import QRCode from 'qrcode'
import "@/css/xiangqingcss.css";
export default {
  name: "xiangqing",
  data() {
    return {
      shown: false,
      showShare: false,
      myimg:'',
      options: [
          { name: '微信', icon: 'wechat' },
        { name: '微博', icon: 'weibo' },
        { name: '复制链接', icon: 'link' },
        { name: '分享海报', icon: 'poster' },
        { name: '二维码', icon: 'qrcode' },
        { name: 'QQ', icon: 'qq' }
      ],
      index:0,
      value:'',
      show: false,
      isShow: true,
      id: this.$route.query.id,
      xqlist: [],
      inputtext: "",
      list: [],
      limit: 10,
      loading: false,
      finished: false,
      hfpllb: [],
      likes: [],
      vals: "",
      uid: localStorage.getItem("trip"),
      ids:''
    };
  },
  created() {
    this.aaa();
    this.bbb();
  },
  methods: {
    // ***********************分享
    onSelect({icon}) {
      this.showShare = false;
      let ShareFn={
        wechat:this.wechatFn,
        weibo:this.weiboFn,
        link:this.linkFn,
        poster:this.posterFn,
        qrcode:this.qrcodeFn,
        qq:this.qqFn,
      };
      ShareFn[icon].call(this)
    },
    wechatFn(){
      console.log('wechat')
    },
    // ********************************复制链接
      linkFn(){
        var input = document.createElement("input");   // js创建一个input输入框
        input.value =location.href;  // 将需要复制的文本赋值到创建的input输入框中，this.ruleForm.url这个是我要复制的内容
        document.body.appendChild(input);    // 将输入框暂时创建到实例里面
         document.body.appendChild(input); // 最后删除实例中临时创建的input输入框，完成复制操作
        input.select();   // 选中输入框中的内容
        document.execCommand("Copy");   // 执行复制操作
        this.$toast('yes')
      },
      // ****************************微博
    weiboFn(){
    let img=this.xqlist.imageSrc[0]||''
    let title=location.href+'(想要看更多文章就来头条）#头条#'
    title=encodeURIComponent(title)
    img=encodeURIComponent(img)
    window.open(`https://service.weibo.com/share/share.php?title=${title}&pic=${img}`)
    },
    // ************************qq
    qqFn(){
      let img=this.xqlist.imageSrc[0]||''
    let title=location.href+'(想要看更多文章就来头条）#头条#'
    let url=location.href
    url=encodeURIComponent(url)
    title=encodeURIComponent(title)
    img=encodeURIComponent(img)
    window.open(` https://connect.qq.com/widget/shareqq/index.html?url=${url}&title=${title}&pics=${img}`)
    },
    // ******************************微信
    wechatFn(){
      let url=location.href
      QRCode.toDataURL(url)
  .then(url => {
    this.shown=true
    this.myimg=url
    console.log(url)
  })
  .catch(err => {
    console.error(err)
  })
    },
    // *****************二维码
    qrcodeFn(){
      let url=location.href
      QRCode.toDataURL(url)
  .then(url => {
    this.shown=true
    this.myimg=url
    console.log(url)
  })
  .catch(err => {
    console.error(err)
  })
    },
    // *********************回复评论面板
    showPopup(index) {
      this.index=index
      this.hfpl()
       this.ids=this.list[index]._id
      this.show = true;
      if(this.show=true){
        this.value=''
      }
    },
    aaa() {
      this.axios
        .post(
          "https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_article_detail",
          {
            article_id: this.id, //文章ID
            user_id: localStorage.getItem("trip"), //用户ID
          }
        )
        .then((res) => {
          console.log(res.data.data);
          this.xqlist = res.data.data;
        });
    },
    fanhui() {
      this.$router.go(-1);
    },
    // ***************************发送点赞
    dianzan() {
      this.axios
        .post(
          " https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/like",
          {
            user_id: localStorage.getItem("trip"), //用户ID
            article_id: this.id, //点击的那条评论的ID
          }
        )
        .then((res) => {
          console.log(res.data);
          this.$toast(res.data.msg);
          this.aaa();
        });
    },
    // ******************************取消点赞
    undianzan() {
      this.axios
        .post(
          " https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/unlike",
          {
            user_id: localStorage.getItem("trip"), //用户ID
            article_id: this.id, //点击的那条评论的ID
          }
        )
        .then((res) => {
          console.log(res.data);
          if ((res.data.msg = "取消点赞成功")) {
            this.$toast("取消点赞成功");
          }
          this.aaa();
        });
    },
    // ****************************发送评论
    pinglun() {
      if (this.uid == null) {
        this.$toast("请登入");
        this.$router.push('./login')
      }
      this.axios
        .post(
          " https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/add_comment",
          {
            user_id: localStorage.getItem("trip"), //用户ID
            article_id: this.id, //文章ID
            content: this.inputtext, //内容
            reply_comment_id: "", //回复的评论ID comment_type为1时有效  comment_type为0时可以不填
            comment_type: 0, //回复类型，0表示回复文章，1表示回复评论
          }
        )
        .then((res) => {
          console.log(res);
          if (res.data.msg == "评论成功") {
            this.$toast("评论成功")
            this.inputtext = "";
            this.onLoad();
          }
        });
    },
    dingyue() {
      this.axios
        .post(
          " https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/add_fav",
          {
            user_id: localStorage.getItem("trip"), //用户ID
            article_id: this.id, //文章ID
          }
        )
        .then((res) => {
          console.log(res.data);
          this.$toast(res.data.msg);
          this.aaa();
        });
    },
    dingyueyes() {
      this.axios
        .post(
          " https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/remove_fav",
          {
            user_id: localStorage.getItem("trip"), //用户ID
            article_id: this.id, //文章ID
          }
        )
        .then((res) => {
          console.log(res);
          if ((res.data.msg = "取消收藏成功")) {
            this.$toast("取消收藏成功");
          }
          this.aaa();
        });
    },
    // ******************************显示隐藏

    // ***************************评论列表
    onLoad() {
      // 异步更新数据
      // setTimeout 仅做示例，真实场景中一般为 ajax 请求
      setTimeout(() => {
        this.axios
          .post(
            " https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_comment_list",
            {
              user_id: localStorage.getItem("trip"), //用户ID
              article_id: this.id, //文章ID
              skip: "0",
              limit: this.limit,
            }
          )
          .then((res) => {
            console.log(res.data.data);
            this.list = res.data.data;
          });

        // 加载状态结束
        this.loading = false;
        this.limit += 10;
        // 数据全部加载完成
        if (this.list.length <= this.limit) {
          this.finished = true;
        }
      }, 1000);
    },
    // ********************回复评论
    hfpl() {
      if (this.uid == null) {
        this.$toast("请登入");
        this.$router.push('./login')
      }
      this.axios
        .post(
          " https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/add_comment",
          {
            user_id: localStorage.getItem("trip"), //用户ID
            article_id: this.id, //文章ID
            content: this.value, //内容
            reply_comment_id: this.ids, //回复的评论ID comment_type为1时有效  comment_type为0时可以不填
            comment_type: 1, //回复类型，0表示回复文章，1表示回复评论
          }
        )
        .then((res) => {
          console.log(res);
          if ((res.data.msg = "评论成功" && this.value != "")) {
            this.$toast("评论成功");
          }
          this.value= "";
          this.bbb();
        });
    },
    // ********************************************回复评论列表
    bbb() {
      this.axios
        .post(
          " https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_reply_list",
          {
            skip: 0, //起始条数
            limit: 10000000, //一次返回的条数
            user_id: localStorage.getItem("trip"), //用户ID
            article_id: this.id, //文章ID
            reply_comment_id: this.ids, //回复的评论ID comment_type为1时有效  comment_type为0时可以不填
            comment_type: "1", //回复类型，0表示回复文章，1表示回复评论
          }
        )
        .then((res) => {
          console.log(res.data);
          this.hfpllb = res.data.data;
        });
    },
    //  ********************************评论点赞
    like(item) {
      this.axios
        .post(
          " https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/comment_like",
          {
            user_id: localStorage.getItem("trip"), //用户ID
            comment_id: item._id, //点击的那条评论的ID
          }
        )
        .then((res) => {
          console.log(res);
          this.$toast(res.data.msg);
          this.likes = res.data;
          this.onLoad();
        });
    },
    unlike(item) {
      this.axios
        .post(
          " https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/comment_unlike",
          {
            user_id: localStorage.getItem("trip"), //用户ID
            comment_id: item._id, //点击的那条评论的ID
          }
        )
        .then((res) => {
          console.log(res);
          this.$toast("取消点赞成功");
          this.onLoad();
        });
    },
  },
};
</script>

