<template>
  <div class="logo">
    <div>
     
    </div>
     <h5 >新头条 叫你随时了解</h5>
     <span>倒计时{{num}}</span>
     
  </div>
</template>

<script>
export default {
    name:'logo',
    data() {
        return {
            num:5,
        }
    },
    created(){
        let time=setInterval(() => {
            this.num--
            if(this.num==0){
                clearInterval(time)
                this.$router.push('./index')
            }
        }, 1000);
    },
}
</script>

<style scoped>
.logo div{width: 70%;background: url('@/assets/R-C\ \(3\).jpg')no-repeat center;height: 90vh;margin: 0 auto;margin-top: -10%;}
.logo h5{position:absolute;font-size: 22px;top:60%;left: 22%;}
.logo span{width: 20%;height: 30px;background: #ededed;display: inline-block;text-align: center;line-height: 30px;border-radius: 100px;position: fixed;top: 0;right:0;}
</style>